:root {
    --colorth: #012632eb
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.header {
    height: 50px;
    background-color: var(--colorth);
    border: 1px solid var(--colorth);
}

.footer {
    height: 100px;
    background-color: var(--colorth);
    border: 1px solid var(--colorth);
    position: absolute;
    bottom: 0;
    width: 100%;
}

.save-btn-div {
    height: 60px;
    border: 2px solid grey;
}

.btn {
    width: 105px;
    height: 45px;
    font-size: 37px;
    margin-top: 7px;
    margin-left: 75px;
    border: none;
    background-color: #d2cdcd;
}

.mainn {
    display: flex;
    justify-content: space-between;

}

/* 
.right-box-container {
    width: 20%;
    background-color: #e6f1f1;
} */

.child-boxes {
    width: 80px;
    height: 80px;
    background-color: rgb(202, 197, 197) !important;
    box-shadow: 0 2px 5px rgb(0, 0, 0);
}

.box {
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    padding: 10px;
    margin-bottom: 50px;
    background-color: #bccbce;
    /* height: 120%; */
}
.popup-div {
    width: 300px;
    height: 300px;
    background-color: #b2afaff5;
    position: absolute;
    cursor: grab;
    left: 36%;
    top: 25%;
    z-index: 99;
    box-shadow: 3px 4px 8px #888888;
  }
  

::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-thumb {
    background: #b0f1f9;

}

::-webkit-scrollbar-track {
    background: var(--colorth);
}

::-webkit-scrollbar-corner {
    background: var(--colorth);
}

.app-container {
    display: flex;
}

.sidebar {
    width: 330px;
    height: 73vh;
    padding: 5px;
    background-color: #bccbce;
    position: relative;
    top: 0;
    left: 0;
    overflow-x: hidden;
}
.border-left-div{
    border-top: 20px solid var(--colorth);
}


.resize-handle {
    width: 2px;
    height: 100%;
    position: absolute;
    top: 0;
    cursor: col-resize;
}

.right-0 {
    right: 0;
}

.left-0 {
    left: 0;
}

.btn-close {
    position: absolute;
    right: 0;
    font-size: 30px;
    color: orangered;
    background-color: #8080800a;
    border: none;

}
.right-bar{
    height: 100%;
    background-color: var(--colorth);
    width: 4px;
}

.childBox-container{
    height: 100vh;
}

.left-main{
    position: relative;
}
.width-2{
    width: 2px;
    background-color:grey;
}